import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import Reiki from '../components/paginas/Reiki.vue'
import Terapia from '../components/paginas/Terapia.vue'
import RegistrosAkashicos from '../components/paginas/RegistrosAkashicos.vue'
import Aromaterapia from '../components/paginas/Aromaterapia.vue'
import FloresDeBach from '../components/paginas/FloresDeBach.vue'
import Bioenergetica from '../components/paginas/Bioenergetica.vue'
import MunayKi from '../components/paginas/MunayKi.vue'
import Meditacion from '../components/paginas/Meditacion.vue'
import BarraDeAccess from '../components/paginas/BarraDeAccess.vue'
import PlantasMedicinales from '../components/paginas/PlantasMedicinales.vue'
import Login from '../components/paginas/Login.vue'
import Tienda from '../components/paginas/Tienda.vue'
import MiCodigo from '../components/paginas/MiCodigo.vue'
import goTo from 'vuetify/es5/services/goto'


Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: (to, from, savedPosition) => {
    let scrollTo = 0

    if (to.hash) {
      scrollTo = to.hash
    } else if (savedPosition) {
      scrollTo = savedPosition.y
    }

    return goTo(scrollTo)
  },
 
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/Reiki',
      name: 'Reiki',
      title: 'Reiki',
      component: Reiki,
    },
    {
      path: '/Terapia',
      name: 'Terapia',
      title: 'Terapia',
      component: Terapia,
    },
    {
      path: '/RegistrosAkashicos',
      name: 'RegistrosAkashicos',
      title: 'RegistrosAkashicos',
      component: RegistrosAkashicos,
    },
    {
      path: '/Aromaterapia',
      name: 'Aromaterapia',
      title: 'Aromaterapia',
      component: Aromaterapia,
    },
    {
      path: '/FloresDeBach',
      name: 'FloresDeBach',
      title: 'FloresDeBach',
      component: FloresDeBach,
    },
    {
      path: '/Bioenergetica',
      name: 'Bioenergetica',
      title: 'Bioenergetica',
      component: Bioenergetica,
    },
    {
      path: '/MunayKi',
      name: 'MunayKi',
      title: 'MunayKi',
      component: MunayKi,
    },
    {
      path: '/Meditacion',
      name: 'Meditacion',
      title: 'Meditacion',
      component: Meditacion,
    },
    {
      path: '/BarraDeAccess',
      name: 'BarraDeAccess',
      title: 'BarraDeAccess',
      component: BarraDeAccess,
    },
    {
      path: '/PlantasMedicinales',
      name: 'PlantasMedicinales',
      title: 'PlantasMedicinales',
      component: PlantasMedicinales,
    },
    {
      path: '/Login',
      name: 'Login',
      title: 'Login',
      component: Login,
    },
    {
      path: '/Tienda',
      name: 'Tienda',
      title: 'Tienda',
      component: Tienda,
    },
    {
      path: '/MiCodigo',
      name: 'MiCodigo',
      title: 'MiCodigo',
      component: MiCodigo,
    }
    
  ],
})
