<template>
    <v-container fluid grid-list-md>
              <v-flex d-flex xs12 sm8 md10 child-flex>
            <v-alert
                    class="text-center"
                    prominent
                    dense
                    dark
                    border="bottom"
                    colored-border
                    type="info"
                    elevation="4"
                    dismissible
                >
                Recuerde que tendrá acceso durante los 30 días posteriores de la realización de la compra, 
                luego debe adquirir un nuevo código de acceso.
            </v-alert>    
        </v-flex>
        <v-layout row wrap justify-center="justify">

            <v-flex d-flex xs12 sm8 md6>
                <v-card style="padding:20%; margin-bottom:0%;">
                    
                    <v-img
                        class="mx-2"
                        :src= "require('@/assets/instagram/MENTEMANIFESTANDOSE-instagram.png')"
                        max-height="100"
                        max-width="100"
                        contain
                    >
                    </v-img>    
                    <br>
                    <v-subheader>MI TALLER</v-subheader>
                    <form
                        ref="form"
                    >
                        <v-select
                            v-model="select"
                            :items="items"
                            :error-messages="errors"
                            label="Seleccione el Taller"
                            data-vv-name="select"
                            required
                        ></v-select>
                        <v-text-field 
                            autofocus
                            v-model="emailAcceso"
                            label="ingrese Mail" 
                            :rules="emailRules" 
                            hide-details="auto"
                            hint="xxx@xxx.com"
                            validate-on-blur
                            ref="emailAcceso"
                            requiered
                        >
                        </v-text-field>
                        <v-text-field 
                            v-model="codigo"
                            label="ingrese su codigo" 
                            type="password" 
                            hide-details="auto"
                            validate-on-blur
                            ref="codigo"
                            requiered
                        >
                        </v-text-field>
                        <v-btn
                            :disabled="valid"
                            class="mr-4"
                            color="deep-purple accent-4"
                            text-color="white"
                            @click="ingresar()"
                            style="margin:12px;"
                            
                        >
                            Ingresar
                        </v-btn>
                        <p><small>Si aun no tenes tu codigo de acceso hace<router-link :to="{ path: 'MiCodigo' }" style="color:black;font-family:Comic Sans MS;">
                            CLICK AQUÍ<v-icon left color="black">mdi-gesture-tap</v-icon></router-link>
                        </small></p>
                    </form>
                </v-card>
            </v-flex>

            
        </v-layout>
    </v-container>
</template>

<script>

export default {
    name:"Login",
    data: () => ({

        
        valid:false,
        codigobase:'1234',
        emailbase:'nan@gmail.com',
        emailAcceso:'',
        codigo:'',
        emailRules:[
            v => !!v || 'ingrese un mail ',
            v => /.+@.+\..+/.test(v) || 'ingrese un mail valido'
        ],
        items: [
            'Registros Akashicos',
            'Item 2',
                
        ],
    }),
    methods: {
        ingresar (){
            if(this.emailAcceso!== this.emailbase){
                alert('mail incorrecto')
                this.$refs.emailAcceso.reset()
                this.$refs.emailAcceso.focus()
            }else if(this.codigo!== this.codigobase){
                alert('codigo incorrecto')
                this.$refs.codigo.reset()
                this.$refs.codigo.focus()
            }else{
                alert('ingreso exitoso')
                this.$refs.form.reset()
                this.$router.push('/tienda')
            }


        }
     
     }
    
}
</script>


